import React, {Component} from 'react';

export default class formAdd extends Component{

    state = {
        name: '',
        price: '',
        year: '',
        description: '',
        color: '',
        select: ''
    };

    onChange = (e) => this.setState({[e.target.name]: e.target.value});

    handleOptionChange = (e) => this.setState({
        color: e.target.value
        });

    onSubmit = (e) => {
        e.preventDefault();
        this.props.addProduct(this.state);
        this.setState({
            name: '',
            price: '',
            year: '',
            description: '',
            color: '',
            select: ''
        });
    };

    render () {
    return (
        <form className="addForm" id="addForm" onSubmit={this.onSubmit}>
            <input
                className="addForm__text"
                name="name"
                placeholder="Название"
                value={this.state.name}
                onChange={this.onChange}
                required
            />
                <label className="addForm__label addForm__label_price">&nbsp; Цена
                    <input
                        className="addForm__text"
                        name="price"
                        placeholder="0"
                        pattern="[0-9]{6,}"
                        value={this.state.price}
                        onChange={this.onChange}
                        required
                    />
                </label>
                <input
                    className="addForm__text"
                    name="year"
                    placeholder="Год"
                    pattern="[0-9]{4}"
                    value={this.state.year}
                    onChange={this.onChange}
                    required
                />
                    <input
                        className="addForm__text"
                        name="description"
                        placeholder="Описание"
                        value={this.state.description}
                        onChange={this.onChange}
                    />
                        <label className="addForm__label addForm__label_colors">Цвет<br/>
                            <label className="container">
                                <input
                                    className="addForm__colorSelect"
                                    name="color"
                                    type="radio"
                                    value="#fff"
                                    checked={this.state.color === '#fff'}
                                    onChange={this.handleOptionChange}
                                /><span
                                    className="checkmark checkmark_white"></span>
                            </label>
                            <label className="container">
                                <input
                                    className="addForm__colorSelect"
                                    name="color"
                                    type="radio"
                                    value="#000"
                                    checked={this.state.color === '#000'}
                                    onChange={this.handleOptionChange}
                                    required
                                /><span
                                    className="checkmark checkmark_black"></span>
                            </label>
                            <label className="container">
                                <input
                                    className="addForm__colorSelect"
                                    name="color"
                                    type="radio"
                                    value="#cbcbcc"
                                    checked={this.state.color === '#cbcbcc'}
                                    onChange={this.handleOptionChange}
                                /><span
                                    className="checkmark checkmark_gray"></span>
                            </label>
                            <label className="container">
                                <input
                                    className="addForm__colorSelect"
                                    name="color"
                                    type="radio"
                                    value="#d74345"
                                    checked={this.state.color === '#d74345'}
                                    onChange={this.handleOptionChange}
                                /><span
                                    className="checkmark checkmark_red"></span>
                            </label>
                            <label className="container">
                                <input
                                    className="addForm__colorSelect"
                                    name="color"
                                    type="radio"
                                    value="#88c504"
                                    checked={this.state.color === '#88c504'}
                                    onChange={this.handleOptionChange}
                                /><span
                                    className="checkmark checkmark_green"></span>
                            </label>
                        </label>
                        <input
                            className="addForm__select"
                            name="select"
                            list="status"
                            placeholder="Статус"
                            value={this.state.select}
                            onChange={this.onChange}
                            required
                        />
                            <datalist id="status">
                                <option key='1' value="В наличии" />
                                <option key='2' value="Ожидается" />
                                <option key='3' value="Нет в наличии" />
                            </datalist>
                            <button className="addForm__submit" type="submit" form="addForm">ОТПРАВИТЬ</button>
        </form>
    )
    }
};