import React, {Component} from 'react';
import Header from './components/layout/header';
import Footer from './components/layout/footer';
import FormAdd from './components/formadd';
import ProductItems from './components/products/productItems';
import uuid from 'uuid';
import axios from 'axios';

export default class App extends Component{
    state = {
        products: []
    };

    componentDidMount() {
        axios.get('https://rawgit.com/Varinetz/e6cbadec972e76a340c41a65fcc2a6b3/raw/90191826a3bac2ff0761040ed1d95c59f14eaf26/frontend_test_table.json')
            .then(res => this.setState({products: res.data}))
    }

    addProduct = (product) => {
        const newProduct = {
            id: uuid.v4(),
            title: product.name,
            price: parseInt(product.price),
            year: product.year,
            description: product.description,
            color: product.color,
            status: product.select
        };

        this.setState({
            products: [newProduct, ...this.state.products]
        })
    };

    delProduct = (id) => {
        this.setState({
            products: [...this.state.products.filter(product => product.id !== id)]
        })
    };

    render() {
        return (
            <>
                <Header/>
                <main className="content">
                    <h1 className="mainTitle">¡Ay caramba!</h1>
                    <FormAdd addProduct={this.addProduct}/>
                    <ProductItems products={this.state.products} delProduct={this.delProduct}/>
                </main>
                <Footer/>
            </>
        );
    }
}
