import React, {Component} from 'react';
import ProductItem from './productItem'
import PropTypes from 'prop-types';

export default class productItems extends Component{

    render(){
        console.log(this.props.products);

        if (this.props.products.length === 0)
            return <h2 className="sectionTitle">Автомобилей пока нет в наличии</h2>

        return(
        <>
            <h2 className="sectionTitle">Автомобили в наличии</h2>
            <div className="productItems">
                <div className="productItem productItem_heading">
                    <div className="productItem__title productItem__title_heading">Название</div>
                    <div className="productItem__color productItem__color_heading">Цвет</div>
                    <div className="productItem__price productItem__price_heading">Цена</div>
                    <div className="productItem__year productItem__year_heading">Год</div>
                    <div className="productItem__status productItem__status_heading">Статус</div>
                    <div className="productItem__btn productItem__btn_heading"></div>
                </div>

            {this.props.products.map((product) => (
                <ProductItem key={product.id} product={product} delProduct={this.props.delProduct}/>
            ))
            }

            </div>
        </>
        )
    }
}
productItems.propTypes = {
    products: PropTypes.array.isRequired
};