import React, {Component} from 'react';
import PropTypes from 'prop-types';

export default class productItem extends Component{

    render(){
        const {id, title, price, year, description, color, status} = this.props.product;
        let localStatus, localColor;

        if (status === 'pednding') {
            localStatus = 'Ожидается'
        } else if (status === 'in_stock') {
            localStatus = 'В наличии'
        } else if (status === 'out_of_stock') {
            localStatus = 'Нет в наличии'
        } else {
            localStatus = status
        }

        if (color === 'red') {
            localColor = '#d74345'
        } else if (color === 'green') {
            localColor = '#88c504'
        } else {
            localColor = color
        }

        return(
            <>
                <div className="productItem">
                    <div className="productItem__title">{title}</div>
                    <div className="productItem__color" style={{backgroundColor: localColor }} />
                    <div className="productItem__price">{price.toLocaleString()}</div>
                    <div className="productItem__desc">{description}</div>
                    <div className="productItem__year">{year}</div>
                    <div className="productItem__status">{localStatus}</div>
                    <button className="productItem__btn" onClick={this.props.delProduct.bind(this, id)}>Удалить</button>
                </div>
            </>
        )
    }
}
productItem.propTypes = {
    product: PropTypes.object.isRequired
};